import {
	uniCard,
	uniIcons,
	uniPagination,
	uniList,
	uniPopup,
	uniListItem,
	uniNavBar
} from '@dcloudio/uni-ui'
// 也可使用此方式引入组件
// import uniBadge from '@dcloudio/uni-ui/lib/uni-badge/uni-badge.vue'
export default {
	components: {
		uniCard,
		uniList,
		uniPopup,
		uniListItem,
		uniIcons,
		uniPagination,
		uniNavBar
	},
	onLoad() {
		const myDate = new Date();
		this.nowdate.year = myDate.getFullYear();
		this.nowdate.month = myDate.getMonth() + 1;
		this.nowdate.day = myDate.getDate();
	},
	onShow() {
		this.getSummary();
		this.getData();
	},
	data() {
		return {
			iseye: false,
			isbudget: false,
			isoperat: false,
			budget: '',
			opearObj: {},
			nowdate: {
				year: "",
				month: "",
				day: ""
			},
			pattern: {
				buttonColor: "#4cd964",
				color: "#fff"
			},
			summary: {
				budgetotal: 0, //当月预算金额
				budgetId: '', // 当月预算金额数据ID
				expenses: 0, //当月支出总额
				income: 0 //当月收入总额
			},
			datalist: [],
			lists: []
		}
	},
	methods: {
		// 获取汇总数据
		getSummary() {
			const _createTime = this.nowdate.year + '-' + this.nowdate.month + '-1';
			const _yearMonth = this.nowdate.year + '-' + this.nowdate.month;
			const dataobj = {
				yearMonth: new Date(_yearMonth).getTime(),
				createTime: new Date(_createTime).getTime()
			}
			uniCloud.callFunction({
				name: 'summary',
				data: dataobj
			}).then((res) => {
				this.summary = res.result;
				this.budget = res.result.budgetotal;
			}).catch((err) => {
				uni.showModal({
					content: `查询失败，错误信息为：${err.message}`,
					showCancel: false
				})
				console.error(err)
			})
		},
		//获取列表数据
		getData() {
			const _date = this.nowdate.year + '-' + this.nowdate.month + '-' + this.nowdate.day;
			const _createTime = new Date(_date).getTime();
			uniCloud.callFunction({
				name: 'get',
				data: {
					createTime: _createTime
				}
			}).then((res) => {

				let extotal = 0,
					intotal = 0;
				res.result.data.forEach(el => {
					el.weekday = this.utils.getMyDay(el.useDate);
					if (el.genre == 1) {
						extotal += Number(el.amount)
					} else if (el.genre == 2) {
						intotal += Number(el.amount)
					}
				})
				let obj = {
					datetime: `${this.nowdate.year}-${this.nowdate.month}-${this.nowdate.day}`,
					budget: `收入：${intotal} 支出：${extotal}`,
					listItem: res.result.data
				}
				this.lists = [];
				this.lists.push(obj);
				
			}).catch((err) => {
				uni.showModal({
					content: `查询失败，错误信息为：${err.message}`,
					showCancel: false
				})
			})
		},
		// 设置预算 
		setbudget() {
			this.isbudget = true;
			this.isoperat = false;
			this.$refs.indpopup.open()
		},
		// 提交预算
		submitBudget() {
			uni.showLoading({
				title: '处理中...'
			})

			let nowmonth = this.nowdate.year + '-' + this.nowdate.month;
			let eventName = "",
				postData = {
					yearMonth: nowmonth,
					createTime: new Date(nowmonth).getTime(),
					budgetTotal: this.budget
				};

			if (this.summary.budgetId) {
				eventName = "updateBudget";
				postData.budgetId = this.summary.budgetId;
			} else {
				eventName = "budget";
			}

			uniCloud.callFunction({
				name: eventName,
				data: postData
			}).then((res) => {
				uni.hideLoading()
				this.$refs.indpopup.close();
				this.isbudget = false;
				uni.showModal({
					content: '设置成功',
					showCancel: false
				})
				this.getSummary();
			}).catch((err) => {
				this.$refs.indpopup.close();
				this.isbudget = false;
				uni.hideLoading()
				uni.showModal({
					content: '设置失败，请重新设置',
					showCancel: false
				})
				console.error(err)
			})
		},
		// 点击搜索按钮
		clickSearch() {
			this.$refs.indpopup.close();
			uni.navigateTo({
				url: './search'
			});
		},
		//   点击某条记录
		handleItem(item) {
			let _item = JSON.stringify(item)
			uni.navigateTo({
				url: './details?item=' + _item
			});
		},
		// 长按某条记录
		handleLong(item) {
			this.opearObj = item;
			this.isoperat = true;
			this.isbudget = false;
			this.$refs.indpopup.open();
		},
		// 修改1 删除 2
		moreOper(val) {
			const _this = this;
			if (val == 1) {
				this.handleItem(this.opearObj)

			} else {
				
				uni.showModal({
					title: '提示',
					content: '是否确认删除此条数据？',
					success: function(res) {
						
						if (res.confirm) {
							uni.showLoading({
								title:"数据删除中..."
							})
							uniCloud.callFunction({
								name: 'remove',
								data: {
									dataId: _this.opearObj._id
								}
							}).then((res) => {
								uni.hideLoading();
								uni.showToast({
									title: `${res.result.msg}`,
									duration: 2000
								});
								_this.getData();
							}).catch((err) => {
								uni.hideLoading();
								uni.showModal({
									content: `操作失败，请重新操作`,
									showCancel: false
								})
								console.error(err)
							})

						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			}
			this.isoperat = false;
			this.$refs.indpopup.close();
		},
		//点击菜单按钮
		handleNavs() {
			console.log('navs')
		}
	}
}
